/* Modal Overlay for all devices */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent black background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Modal Content */
.modal-content {
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  width: 80%;
  max-width: 500px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.3);
  position: relative;
}

/* Close Button */
.modal-close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  font-size: 24px;
  cursor: pointer;
  color: black;
  transition: color 0.3s ease;
}

.modal-close-button:hover {
  color: #ff0000; /* Red on hover */
}

/* Modal Title */
.modal-title {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 10px;
  text-align: center;
  font-family: Arial, sans-serif;
}

/* Modal Body */
.modal-body {
  font-size: 16px;
  text-align: center;
  font-family: Arial, sans-serif;
  line-height: 1.5;
}

/* Responsive modal for mobile devices */
@media (max-width: 768px) {
  .modal-content {
    width: 95%; /* Adjust width for smaller screens */
    max-width: 100%; /* Make sure the modal takes the full screen width on mobile */
    padding: 15px;
    font-size: 18px; /* Slightly larger font size for readability */
  }

  .modal-title {
    font-size: 20px; /* Adjust modal title size */
  }

  .modal-body {
    font-size: 14px; /* Adjust body text size */
  }

  .modal-close-button {
    font-size: 20px; /* Adjust close button size */
  }
}
