/* Wrapper for horizontal scrolling with buttons */
.card-container-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  
  max-width: 100vw;
  overflow: hidden; /* Ensures no scrollbars appear */
  background-color: white;
}

/* Horizontal scrolling area */
.card-container {
  display: flex; /* Crucial for horizontal layout */
  overflow-x: auto; /* Allows horizontal scrolling */
  scroll-behavior: smooth;
  padding: 40px 0;
  
  white-space: nowrap; /* Prevents wrapping into a new line */
}

.card-container::-webkit-scrollbar {
  display: none; /* Hide scrollbar for cleaner look */
}


/* Scroll buttons */
.scroll-btn {
  position: absolute;
  bottom: 15px; /* Adjust the distance from the bottom */
  background-color: black;
  border: none;
  color: white;
  font-size: 15px;
  padding: 10px;
  cursor: pointer;
  z-index: 1;
  border-radius: 15%; /* Round buttons */
}

/* Both buttons stacked horizontally in the right corner */
.scroll-btn.left {
  right: 60px; /* Left button positioned slightly to the left of the right button */
}

.scroll-btn.right {
  right: 20px; /* Right button closer to the bottom-right corner */
}

.scroll-btn:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

