/* Styling for individual cards */
.card {
  flex: 0 0 auto;
  width: 450px; 
  height: 600px; 
  margin: 0 10px;
  background-size: cover;
  background-position: center;
  border-radius: 20px;
  display: flex;
  align-items: flex-end;
  box-shadow: 0px 4px 15px rgba(104, 104, 104, 0.3);
  transition: transform 0.3s ease;
  position: relative;
  color: white;
}

.card:hover {
  transform: scale(1.05); 
}

.card-content {
  padding: 40px;
  background-color: rgba(0, 0, 0, 0.4); 
  border-radius: 20px;
  width: 100%;
  text-align: center; /* Keep all text centered */
}

/* Card Subtitle */
.card-subtitle {
  font-size: 14px;
  opacity: 0.8;
  text-align: center; /* Consistent text alignment */
  font-family: Arial, sans-serif;
}

/* Card Title */
.card-title {
  font-size: 22px;
  font-weight: bold;
  margin: 5px 0;
  text-align: center;
  font-family: Arial, sans-serif;
}

/* Card Description */
.card-description {
  font-size: 16px;
  text-align: center;
  font-family: Arial, sans-serif;
  line-height: 1.5;
}

/* Styling for the "+" button */
.card-plus-button {
  position: absolute;
  bottom: 20px;
  right: 20px;
  background-color: rgb(7, 7, 7);
  color: rgb(255, 255, 255);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.card-plus-button:hover {
  background-color: #eaeaea;
  color: black;
  transform: scale(1.1);
}

/* Media queries for responsiveness */

/* Tablet version */
@media (max-width: 1024px) {
  .card {
    width: 350px;  /* Reduce width for tablets */
    height: 500px; /* Adjust height */
  }
  
  .card-content {
    padding: 30px;
  }

  .card-title {
    font-size: 17px;
  }

  .card-description {
    font-size: 14px;
  }
  /* Card Subtitle */
.card-subtitle {
  font-size: 11px;
  opacity: 0.8;
  text-align: center; /* Consistent text alignment */
  font-family: Arial, sans-serif;
}
}

/* Mobile version */
@media (max-width: 768px) {
  .card {
    width: 280px;  /* Reduce width further for mobile */
    height: 400px; /* Adjust height */
  }
  
  .card-content {
    padding: 20px;
  }

  .card-title {
    font-size: 14px;
  }

  .card-subtitle {
    font-size: 9px;
    opacity: 0.8;
    text-align: center; /* Consistent text alignment */
    font-family: Arial, sans-serif;
  }

  .card-description {
    font-size: 3px;
  }


  .card-plus-button {
    width: 35px;
    height: 35px;
    font-size: 18px;
  }
}
